.spinner {
  position: fixed;
  top: 400px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: conic-gradient(#000000b7 10%, #ffffff);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 9px), #000 0);
  animation: spinner-zp9dbg 1s infinite linear;
}

@keyframes spinner-zp9dbg {
  to {
    transform: rotate(1turn);
  }
}
